import useLanguage from 'hooks/useLanguage';
import React, { createContext, useState } from 'react';

export const CurrencyConvContext = createContext();

export default function CurrencyConverter({ children }) {
  const { lang } = useLanguage();
  const usd = lang.usd;
  const dotBtc = lang.brand;
  const [converter, setConverter_] = useState(lang.brand);
  const localConverter = window.localStorage.getItem('converter');
  const setConverter = value => {
    setConverter_(value);
    window.localStorage.setItem('converter', value);
  };

  if (localConverter == null) {
    window.localStorage.setItem('converter', converter);
  }

  return (
    <CurrencyConvContext.Provider
      value={{ converter, setConverter, usd, dotBtc }}
    >
      {children}
    </CurrencyConvContext.Provider>
  );
}
